"use client";

import { useState, useEffect } from "react";
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogFooter,
} from "@/components/ui/dialog";
import { Label } from "@/components/ui/label";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";
import { Select, SelectTrigger, SelectItem, SelectContent, SelectValue } from "@/components/ui/select";

interface Adult {
  name: string;
  role: string;
}

interface Excursion {
  id: string;
  title: string;
  numStudents: number;
  responsibleAdults: Adult[];
  date: string;
  time: string;
}

export default function Page() {
  const [excursions, setExcursions] = useState<Excursion[]>([]);
  const [isDialogOpen, setIsDialogOpen] = useState(false);
  const [newExcursion, setNewExcursion] = useState({
    title: "",
    numStudents: "",
    date: "",
    time: "",
  });
  const [adult, setAdult] = useState<Adult>({ name: "", role: "" });
  const [responsibleAdults, setResponsibleAdults] = useState<Adult[]>([]);

  // ----------------------------------------------------------
  //        GET — Cargar excursiones desde PocketBase
  // ----------------------------------------------------------
  useEffect(() => {
    fetch("http://127.0.0.1:8090/api/collections/Salidas/records?page=1&perPage=30")
      .then(res => res.json())
      .then(data => {
        const parsed = data.items.map((item: any) => ({
          id: item.id,
          title: item.Titulo_Salida,
          numStudents: 0,
          responsibleAdults: [{ name: item.Responsable, role: item.Rol_Reponsable }],
          date: item.Fecha_Salida,
          time: item.Horario_Salida,
        }));
        setExcursions(parsed);
      })
      .catch(console.error);
  }, []);

  // ----------------------------------------------------------
  //      Función para agregar adultos responsables
  // ----------------------------------------------------------
  const handleAddAdult = () => {
    if (adult.name && adult.role) {
      setResponsibleAdults([...responsibleAdults, adult]);
      setAdult({ name: "", role: "" });
    }
  };

  // ----------------------------------------------------------
  //        POST — Crear excursión en PocketBase
  // ----------------------------------------------------------
  const handleAddExcursion = () => {
    fetch("http://127.0.0.1:8090/api/collections/Salidas/records", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        Fecha_Salida: newExcursion.date,
        Horario_Salida: newExcursion.time,
        Responsable: responsibleAdults[0]?.name || "",
        Rol_Reponsable: responsibleAdults[0]?.role || "",
        Titulo_Salida: newExcursion.title,
      }),
    })
      .then(res => res.json())
      .then(data => {
        const newItem: Excursion = {
          id: data.id,
          title: data.Titulo_Salida,
          numStudents: 0,
          responsibleAdults: [
            { name: data.Responsable, role: data.Rol_Reponsable },
          ],
          date: data.Fecha_Salida,
          time: data.Horario_Salida,
        };

        setExcursions([...excursions, newItem]);

        // limpiar estados
        setNewExcursion({ title: "", numStudents: "", date: "", time: "" });
        setResponsibleAdults([]);
        setIsDialogOpen(false);
      })
      .catch(console.error);
  };

  return (
    <div className="p-6">
      <h1 className="text-3xl font-bold mb-4">Excursiones</h1>

      {/* Botón abrir Modal */}
      <Button onClick={() => setIsDialogOpen(true)}>Nueva Excursión</Button>

      {/* Listado de excursiones */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mt-6">
        {excursions.map((exc) => (
          <div key={exc.id} className="p-4 border rounded-lg shadow">
            <h2 className="font-bold text-xl">{exc.title}</h2>
            <p><strong>Fecha:</strong> {exc.date}</p>
            <p><strong>Horario:</strong> {exc.time}</p>
            <p><strong>Responsable:</strong> {exc.responsibleAdults[0]?.name}</p>
          </div>
        ))}
      </div>

      {/* Modal */}
      <Dialog open={isDialogOpen} onOpenChange={setIsDialogOpen}>
        <DialogContent className="max-w-lg">
          <DialogHeader>
            <DialogTitle>Nueva Excursión</DialogTitle>
          </DialogHeader>

          {/* Nombre */}
          <Label className="mt-2">Nombre de la Excursión</Label>
          <Input
            value={newExcursion.title}
            onChange={(e) =>
              setNewExcursion({ ...newExcursion, title: e.target.value })
            }
            placeholder="Ej: Museo de Ciencias Naturales"
          />

          {/* Número de alumnos */}
          <Label className="mt-4">Número de Alumnos</Label>
          <Input
            value={newExcursion.numStudents}
            onChange={(e) =>
              setNewExcursion({ ...newExcursion, numStudents: e.target.value })
            }
            placeholder="Ej: 30"
            type="number"
          />

          {/* Adultos */}
          <div className="mt-4 border p-3 rounded">
            <Label>Adultos Responsables</Label>

            <Input
              value={adult.name}
              onChange={(e) => setAdult({ ...adult, name: e.target.value })}
              placeholder="Nombre"
              className="mt-2"
            />

            <Select
              onValueChange={(value) =>
                setAdult({ ...adult, role: value })
              }
            >
              <SelectTrigger className="mt-2">
                <SelectValue placeholder="Selecciona un rol" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="Profesor">Profesor</SelectItem>
                <SelectItem value="Preceptor">Preceptor</SelectItem>
                <SelectItem value="Directivo">Directivo</SelectItem>
              </SelectContent>
            </Select>

            <Button className="mt-2 w-full" onClick={handleAddAdult}>
              + Agregar Adulto
            </Button>

            {responsibleAdults.length > 0 && (
              <ul className="mt-2 text-sm">
                {responsibleAdults.map((a, index) => (
                  <li key={index}>• {a.name} ({a.role})</li>
                ))}
              </ul>
            )}
          </div>

          {/* Fecha / Hora */}
          <div className="grid grid-cols-2 gap-4 mt-4">
            <div>
              <Label>Fecha</Label>
              <Input
                value={newExcursion.date}
                onChange={(e) =>
                  setNewExcursion({ ...newExcursion, date: e.target.value })
                }
                placeholder="DD/MM"
              />
            </div>

            <div>
              <Label>Horario</Label>
              <Input
                value={newExcursion.time}
                onChange={(e) =>
                  setNewExcursion({ ...newExcursion, time: e.target.value })
                }
                placeholder="HH:MMhs"
              />
            </div>
          </div>

          <DialogFooter className="mt-4">
            <Button variant="outline" onClick={() => setIsDialogOpen(false)}>
              Cancelar
            </Button>

            <Button onClick={handleAddExcursion}>Agregar Excursión</Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>
    </div>
  );
}
